.diagram-webmaster {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 2;
	img {
		display: block;
		height: auto;
		width: 100%; }
	&__board {
		span {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 100%;
			text-transform: uppercase;
			color: #fb1d95;
			font-size: 14px;
			@include robotocondensed; } }
