$icons: (0:0);
$icons: map-merge($icons,('api': (X: -110px, Y:-110px, W: 39px, H: 26px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('card': (X: -110px, Y:-55px, W: 43px, H: 45px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('discount': (X: 0px, Y:-55px, W: 45px, H: 45px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('graph': (X: -55px, Y:-110px, W: 45px, H: 39px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('guard': (X: -165px, Y:-55px, W: 33px, H: 45px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('help-new': (X: 0px, Y:-110px, W: 45px, H: 39px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('help': (X: 0px, Y:0px, W: 45px, H: 45px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('link': (X: -110px, Y:0px, W: 45px, H: 45px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('money': (X: -210px, Y:0px, W: 29px, H: 45px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('profile': (X: -165px, Y:0px, W: 35px, H: 45px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('sale': (X: -55px, Y:-55px, W: 45px, H: 45px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('worker': (X: -55px, Y:0px, W: 45px, H: 45px, TW: 239px, TH: 149px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

