.out {
	min-height: 320px;
	overflow: hidden; }
.inner {
	@extend %inner;
	position: relative;
	@include clr; }
.section1 {
	background-color: #7200d4;
	height: 100vh;
	min-height: 800px;
	.inner {
		height: 100%; } }
.section3 {
	background-color: #03162d;
	position: relative;
	min-height: 948px;
	&:before {
		content: '';
		display: block;
		position: absolute;
		background: url(../img/lines-bg.png) no-repeat center;
		background-size: cover;
		top: 0;
		right: 0;
		left: 38px;
		height: 1218px;
		z-index: 2; }
	.inner {
		padding-top: 84px;
		padding-bottom: 112px;
		z-index: 2; }
	.title-primary {
		margin-bottom: 81px; } }
.section4 {
	background-color: #121c30;
	min-height: 949px;
	position: relative;
	&:before {
		content: '';
		display: block;
		position: absolute;
		background: url(../img/lines-bg.png) no-repeat center;
		background-size: cover;
		top: 0;
		right: 0;
		left: 38px;
		height: 1218px;
		z-index: 1; }
	.inner {
		padding-top: 112px;
		padding-bottom: 112px;
		z-index: 4; }
	.title-primary {
		margin-bottom: 81px; } }
.section5 {
	background: #131d31 url(../img/bg-sec5.jpg) no-repeat center top;
	background-size: cover;
	.inner {
		padding-top: 110px;
